var routeEnter = require('routeEnter');

module.exports = Object.assign({
    path: "register",
    breadcrumbName: "注册",
    getComponent: function(location, cb) {
        require.ensure([], function(require) {
            cb(null, require("./components/index.jsx"))
        }, 'regFrame')
    },
    getIndexRoute(nextState, cb) {
        require.ensure([], function(require) {
            cb(null, {
                breadcrumbName: '首页',
                component: require("./components/register.jsx")
            })
        }, 'register')
    },
    getChildRoutes(nextState, cb) {
        require.ensure([], (require) => {
            cb(null, [
                require("./routes/success"),
                require("./routes/agreement")
            ])
        })
    }
}, routeEnter);


